<template>
    <div class="item-list">
        <div class="tab-nav">
        <cube-tab-bar
            class=""
            v-model="selectedLabelDefault"
            :data="tabs"
            showSlider
            @click="clickHandler"
            >
        </cube-tab-bar>
        </div>
        <div class=""></div>
    </div>
</template>
<script>
import { itemClassifyList } from '@/utils/generic.js';
export default {
    data() {
        return {
            selectedLabelDefault: itemClassifyList[0].classifyName,
            tabs: []
        }
    },
    created() {
        itemClassifyList.forEach(item => {
            this.tabs.push({
                label: item.classifyName,
                classifyId: item.classifyId,
                classifyName: item.classifyName,
            });
        });
    },
    methods: {
        clickHandler (label) {
            // if you clicked home tab, then print 'Home'
            console.log(label)
        },
    }
}
</script>
<style lang="stylus" scoped>
.item-list {
    .tab-nav {
        height: 80px;
        font-size: 28px;
        font-weight: bold;
        color: #666666;
        background-color: #FFFFFF;
        .cube-tab-bar {
            height: 40px;
        }
    }
}
</style>


