<template>
    <div class="product-page">
        <div class="search-wrap" v-show="!showSearch">
			<div class="search" @click="_onSearch">
				<i class="search-i"></i>
				<span class="search-text">{{text}}</span>
			</div>
		</div>
        <product-list :text="text" v-show="!showSearch" ref="productList"></product-list>
        <!-- <item-list></item-list> -->
		<search v-show="showSearch" @close="_close"></search>
    </div>
</template>
<script>
import ProductList from './component/product-list';
import ItemList from './component/item-list';
import search from './component/search';
export default {
    name: 'productSelect',
    data() {
        return {
			text: '',
			showSearch: false,
        }
    },
    components: {
        ProductList,
		ItemList,
		search,
    },
    created() {
        this.text = this.$route.query.text;
    },
    methods: {
        _onSearch() {
			this.showSearch = true;
		},
		_close(text) {
			if (text !== '') {
				this.text = text;
				this.$refs.productList._queryItemList();
			}
			this.showSearch = false;
		},
    }
}
</script>
<style lang="stylus" scoped>
.product-page {
    font-family:HiraginoSansGB-W3;
    background: #F2F2F2;
    width: 100%;
    height: 100%;
    .search-wrap {
		background-color: #ffffff;
		padding: 12px 0 16px 0;
		.search {
			margin: 0 auto;
			width: 700px;
			height: 60px;
			line-height: 60px;
			background: rgba(242,242,242,1);
			border-radius: 30px;
			text-align: left;
            padding-left: 24px;
			.search-i {
				vertical-align: middle;
				display: inline-block;
				width: 32px;
				height: 32px;
				background-image: url(../../../static/image/common/icon_search@2x.png);
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
			.search-text {
				display: inline-block;
				font-size: 24px;
				color: rgba(102,102,102,1);
			}
		}
	}
}
</style>


