<template>
    <div class="product-list">
        <cube-scroll>
        <ul class="list-wrapper">
            <li v-for="(item, index) in recommendData" class="list-item" :key="index">
                <list :item="item"></list>
            </li>
            <li v-if="recommendData.length === 0" class="no-list">
                暂无商品~
            </li>
        </ul>
        </cube-scroll>        
    </div>
</template>
<script>
import list from './list';
import { queryItemList } from "@/api/product.js";
export default {
    data() {
        return {
            recommendData: []
        }
    },
    props: {
        text: {
            type: String,
            default() {
                return {}
            },   
        }
    },
    created() {
        this._queryItemList();
    },
    components: {
        list,
    },
    methods: {
        _queryItemList() {
            let params = {
                itemName: this.text,
            }
            queryItemList(params).then(res => {
                this.recommendData = res.goodsList;
            }).catch(err => {

            })
        }
    }
}
</script>
<style lang="stylus" scoped>
.product-list {
    height: calc(100vh - 88px);
    .list-wrapper {
        background-color: #FFFFFF;
        padding: 0 30px;
        .no-list {
            height: 80px;
            line-height 80px;
            text-align: center;
            font-size: 28px;
        }
    }
}
</style>


