<template>
    <div class="item">
        <div class="img">
            <img :src="`/picture/${item.itemImg}`" alt="">
        </div>
        <div class="info">
            <div class="top">
                <p class="name">{{item.itemName}}</p>
                <p class="tip">{{item.goodsTexture}}</p>
            </div>
            <div class="price">￥{{item.itemPrice}} <span class="cm">/件</span></div>
        </div>
        <div class="buy-num">
            <i class="add"></i>
            <span class="num">{{0}}</span>
            <i class="less"></i>
        </div>        
    </div>
</template>
<script>
export default {
    data() {
        return {}
    },
    props: {
        item: {
            type: Object,
            default() {
                return {}
            }
        }
    }
}
</script>
<style lang="stylus" scoped>
.item {
                width: 100%;
                display: flex;
                align-items: center;
                padding: 20px 0;
                border-bottom: 0.5px solid #F2F2F2;
                position: relative;
                .img {
                    width:180px;
                    height:120px;
                    border-radius:8px;
                    img {
                        width:180px;
                        height:120px;
                        border-radius:8px;
                    }
                }
                .info {
                    height:120px;
                    padding-left: 21px;
                    .top {
                        margin-top: 10px;
                        position: relative;
                        .name {
                            vertical-align: top;
                            display: inline-block;
                            font-size: 28px;
                            font-family: HiraginoSansGB-W3;
                            color: #333333;
                        }
                        .tip {
                            vertical-align: top;
                            display: inline-block;
                            font-size: 18px;
                            line-height: 24px;
                            font-family: HiraginoSansGB-W3;
                            color: #ffffff;
                            padding: 0 6px;
                            background: #F89771;
                            border-radius:8px;
                        }
                    }
                    .price {
                        margin-top: 45px;
                        font-size: 24px;
                        font-family: HiraginoSansGB-W3;
                        font-weight: bold;
                        color: #F43030;
                        .cm {
                            font-size: 18px;
                            color: #666666;
                        }    
                    }
                }
                .buy-num {
                    position: absolute;
                    right: 0px;
                    .add {
                        display: inline-block;
                        width: 40px;
                        height: 40px;
                        background-image: url(../../../../static/image/product/add@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }
                    .less {
                        display: inline-block;
                        width: 40px;
                        height: 40px;
                        background-image: url(../../../../static/image/product/js@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;                
                    }
                    .num {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 24px;
                        font-family: HiraginoSansGB-W3;
                        font-weight: bold;
                        color: #333333;
                        height: 40px;
                        line-height: 40px;
                        padding: 0 14px;
                    }
                }    
}
</style>


